html, body, #root {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  background-color: whitesmoke; }

button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  min-width: 60px;
  border-radius: 4px;
  background-color: #00adb7;
  padding: 9px 18px;
  color: #c7d3d8;
  overflow: hidden;
  transition: 0.25s background-color;
  font-size: 16px;
  border: none;
  cursor: pointer;
  outline: none; }
  button:hover {
    background-color: #007d84; }



